import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	data() {
		return {
			tablist:['服务',"简介",'联系人'],
			tabindex:0,
			isMe:false,
			show:false,
			company_id:'',
			info:{},
			serviceList:[],
			introduction:{},
			contact:{},
			is_attention:10
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: this.info.name,
		  imageUrl:this.info.img?this.info.img:app.shareImg,
		  path: '/pagesTwo/MyBusiness/MyBusiness?id='+this.company_id
		}
	},
	onLoad(e) {
		// console.log(e)
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		if(uni.getStorageSync("company_id") == e.id){
			this.isMe = true
		}else{
			this.isMe = false
		}
		this.company_id = e.id
		// this.getStatus()
		this.getCompany()
	},
	methods: {
		gogo(){
			uni.openLocation({
			    latitude: Number(this.introduction.lnglat.lat),
			    longitude: Number(this.introduction.lnglat.lng),
			    success: function () {
			        console.log('success');
			    }
			});
		},
		// 服务详情
		ServiceDetails(item){
			// console.log(item)
			// 0就是 正常进详情
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'/pagesFour/ServiceDetails/ServiceDetails?id='+item.id +'&status=0'+"&user_id=" + item.user_id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		guanzhu(){
			
			if(uni.getStorageSync('userId')){
				// 0就是没关注要关注
				if(this.is_attention==0){
					this.$api.attention_company({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						type:1,
						id:this.company_id
					}).then(res=>{
						// console.log(res)
						if(res.data.code=="200"){
							uni.showToast({
							    title: '关注成功',
							    duration: 2000,
								icon:'none'
							});
							this.is_attention = 1
							this.info.attention++
						}
					})
				}else{
					this.$api.attention_company({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						type:0,
						id:this.company_id
					}).then(res=>{
						if(res.data.code=="200"){
							uni.showToast({
							    title: '取消关注',
							    duration: 2000,
								icon:'none'
							});
							this.is_attention = 0
							this.info.attention--
						}
					})
				}
				
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
		},
		getCompany(){
			this.$api.company_detail({
				id:this.company_id,
				token: uni.getStorageSync("token")||'',
				user_id: uni.getStorageSync("userId")||'',
			}).then(res=>{
				if(res.data.code=="200"){
					// console.log(res.data.data.is_attention)
					uni.hideLoading();
					this.info = res.data.data.data
					uni.setStorageSync('service',JSON.stringify({view:this.info.view,companyId:this.company_id}))
					this.is_attention = res.data.data.is_attention
					this.serviceList = res.data.data.service
					this.introduction = res.data.data.introduction
					this.contact = res.data.data.contact
					this.show = true
				}
			})
		},
		changeSwiper(e){
			this.tabindex = e.detail.current
		},
		tab(ind){
			this.tabindex = ind
		},
		// 产品详情
		myProduct(item){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'/pagesTwo/MyProduct/MyProduct?id='+item.id + "&type="+item.type
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 编辑企业
		edit(){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'/pagesTwo/EditEnterpriseIndex/EditEnterpriseIndex?status=0'
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		tabCheck(ind){
			this.tabCurrent = ind
		},
		MyInfo(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/MyInfo/MyInfo?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		tel(){
			if(uni.getStorageSync('userId')){
				uni.makePhoneCall({
					phoneNumber:this.introduction.contact,
					success:()=>{
						this.$api.company_add_count({
							id:this.company_id
						}).then(res=>{
							// console.log(res)
							this.info.contact_amount++
							
						})
					}
				})
				
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
			
		},
		url(){
			if(this.introduction.shop_url){
				uni.navigateTo({
					url:'../../pagesMy/view/view?url='+this.introduction.shop_url
				})
			}
		},
		all(){
			this.more = !this.more
			this.unfold = !this.unfold
		},
		getStatus(){
			if(this.isMe){
				this.$api.myself_company({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId")
				}).then(res=>{
					// console.log(res)
					if(res.data.code === '1054'){
						uni.redirectTo({
							url:'../../pagesTwo/EnterpriseCertification/EnterpriseCertification'
						})
					}else if(res.data.code === '200'){
						this.id = res.data.data.id
						this.enterpriseId = res.data.data.id
						this.getCompany()
					}
				})
			}else{
				this.getCompany()
			}
			
		},
		
		getEnterprise(){
			this.$api.corporate_home_page({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				company_id:this.company_id
			}).then(res=>{
				// console.log(res)
				this.enterprise = res.data.data
				this.userList = res.data.data.user_club_data
				this.$nextTick(()=>{
					  let query1 = uni.createSelectorQuery()
					  query1.selectAll(".middle-content").boundingClientRect(data => {
							if(data[0].height>66){
								this.unfold = true
								this.btn = true
							}
					  }).exec()
					  
					  // console.log(this.listtui)
				})
			})
		},
		getShop(){
			this.$api.corporate_shop({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				company_id:this.company_id
			}).then(res=>{
				this.shopList = res.data.data
			})
			
		},
		getServer(){
			this.$api.corporate_product_service({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				company_id:this.company_id
			}).then(res=>{
				this.serverList = res.data.data
			})
			
		}
		
	}
}
